import { modifyOrderDoctor } from '../../apis/order'

const order = {
  state: {},
  mutations: {},
  actions: {
    ModifyOrderDoctor({commit}, orderInfo) {
      for (let i in orderInfo) {
        orderInfo[i] = orderInfo[i].trim()
      }
      return new Promise((resolve, reject) => {
        let {order, ptel, dtel} = orderInfo
        modifyOrderDoctor(order, ptel, dtel).then(response => {
          resolve(response.data)
        }).catch(error => {
          reject(new Error(error))
        })
      })
    }
  }
}

export default order



