import request from '../utils/request'

export function modifyOrderDoctor(sn, ptel, dtel) {
  const data = {
    order_sn: sn,
    ptel,
    dtel
  }
  return request({
    url: 'order/modify_doctor',
    method: 'post',
    data: JSON.stringify(data)
  })
}



