import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/dashboard'
    },
    {
      path: '/',
      component: resolve => require(['../views/home/Home.vue'], resolve),
      meta: {title: '管理系统'},
      children: [
        {
          path: '/dashboard',
          meta: {title: '首页'},
          component: resolve => require(['../views/home/HelloWorld.vue'], resolve)
        },
        {
          path: '/order/edit-doctor',
          meta: {title: '修改订单医生'},
          component: resolve => require(['../views/order/editDoctor.vue'], resolve)
        }
      ]
    }
  ]
})



